<template>
  <div style="width: 100%; min-height: 100vh; padding: 0 10px; boxSizing: border-box;">
    <!--  -->
    <div :style='{"width":"100%","padding":"20px","margin":"10px auto","borderRadius":"27px","background":"#bfd2fc","boxSizing":"border-box"}' class="breadcrumb-preview">
      <el-breadcrumb :separator="''" :style='{"fontSize":"14px","lineHeight":"1"}'>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item v-for="(item, index) in breadcrumbItem" :key="index">{{item.name}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!--  -->
    <div class="list-preview" :style='{"width":"100%","boxShadow":"0px 4px 10px 0px rgba(0,0,0,0.302)","margin":"20px auto","position":"relative","background":"#fff","boxSizing":"border-box","padding":"0 10px"}'>
      <!--  -->
      <el-form
          class="center-form-pv"
          :style="{ width: '100%', padding: '20px 6px', margin: '0 auto', background: 'rgba(0,0,0,0.102)', boxSizing: 'border-box' }"
          :inline="true"
          :model="searchForm"
      >
        <el-row :style="{ flexDirection: 'row', display: 'flex', flexWrap: 'wrap', justifyContent: 'flex-start', gap: '20px' }">
          <div
              :style="{ margin: '0', flexDirection: 'column', display: 'flex', minWidth: '200px', flex: '1 1 200px' }"
          >
            <label
                :style="{
                margin: '0',
                color: '#000',
                textAlign: 'left',
                display: 'inline-block',
                width: '100%',
                lineHeight: '40px',
                fontSize: '14px',
                fontWeight: '500',
                height: '40px',
              }"
                class="item-label"
            ></label
            >
            <el-input
                v-model="searchForm.yuyuedanhao"
                placeholder=""
                clearable
                style="width: 100%;"
            ></el-input>
          </div>
          <div
              :style="{ margin: '0', flexDirection: 'column', display: 'flex', minWidth: '200px', flex: '1 1 200px' }"
              class="select"
              label=""
              prop="qiandaozhuangtai"
          >
            <label
                :style="{
                margin: '0',
                color: '#000',
                textAlign: 'left',
                display: 'inline-block',
                width: '100%',
                lineHeight: '40px',
                fontSize: '14px',
                fontWeight: '500',
                height: '40px',
              }"
                class="item-label"
            ></label
            >
            <el-select
                @change="qiandaozhuangtaiChange"
                clearable
                v-model="searchForm.qiandaozhuangtai"
                placeholder=""
                style="width: 100%;"
            >
              <el-option
                  v-for="(item, index) in qiandaozhuangtaiOptions"
                  v-bind:key="index"
                  :label="item"
                  :value="item"
              ></el-option>
            </el-select>
          </div>
          <div
              :style="{ margin: '0', flexDirection: 'column', display: 'flex', minWidth: '200px', flex: '1 1 200px' }"
              class="select"
              label=""
              prop="qiantuizhuangtai"
          >
            <label
                :style="{
                margin: '0',
                color: '#000',
                textAlign: 'left',
                display: 'inline-block',
                width: '100%',
                lineHeight: '40px',
                fontSize: '14px',
                fontWeight: '500',
                height: '40px',
              }"
                class="item-label"
            ></label
            >
            <el-select
                @change="qiantuizhuangtaiChange"
                clearable
                v-model="searchForm.qiantuizhuangtai"
                placeholder=""
                style="width: 100%;"
            >
              <el-option
                  v-for="(item, index) in qiantuizhuangtaiOptions"
                  v-bind:key="index"
                  :label="item"
                  :value="item"
              ></el-option>
            </el-select>
          </div>
          <div :style="{ margin: 'auto 0 0', minWidth: '120px' }">
            <el-button
                :style="{
                border: '1px solid #97C9D6',
                cursor: 'pointer',
                padding: '0 24px',
                boxShadow: '0px 2px 2px 0px #78ABC3',
                outline: 'none',
                margin: '10px 0 0',
                color: '#78ABC3',
                borderRadius: '30px',
                background: '#fff',
                width: '100%',
                fontSize: '14px',
                height: '40px',
              }"
                type="success"
                @click="search()"
            ></el-button
            >
          </div>
        </el-row>
      </el-form>

      <!--  -->
      <el-table
          class="custom-table"
          :stripe="false"
          :style="{
          padding: '0',
          boxShadow: '0px 4px 10px 0px rgba(0,0,0,0.3020)',
          borderColor: '#eee',
          borderRadius: '8px',
          borderWidth: '1px 0 0 1px',
          background: '#fff',
          width: '100%',
          margin: '20px auto',
          borderStyle: 'solid',
          height: 'fit-content',
          boxSizing: 'border-box',
          overflow: 'visible',
        }"
          :data="dataList"
          v-loading="dataListLoading"
          border
      >
        <el-table-column
            type="index"
            label=""
            width="80"
            align="center"
            class-name="index-column"
        >
          <template #default="{ $index }">
            <span class="index-badge">{{ $index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column
            :resizable="true"
            :sortable="false"
            prop="yuyuedanhao"
            label=""
            min-width="120"
            align="center"
        >
          <template slot-scope="scope">
            {{ scope.row.yuyuedanhao }}
          </template>
        </el-table-column>
        <el-table-column
            prop="mingcheng"
            label=""
            min-width="120"
            header-align="center"
            align="center"
        >
          <template #default="{ row }">
            <div class="text-ellipsis" :title="row.mingcheng">
              {{ row.mingcheng || "-" }}
            </div>
          </template>
        </el-table-column>
        <el-table-column
            :resizable="true"
            :sortable="false"
            prop="tupian"
            label=""
            min-width="150"
            align="center"
        >
          <template slot-scope="scope">
            <div v-if="scope.row.tupian" style="display: inline-block;">
              <img
                  v-if="scope.row.tupian.substring(0, 4) == 'http'"
                  :src="scope.row.tupian.split(',')[0]"
                  width="80"
                  height="80"
                  style="object-fit: cover; border-radius: 4px;"
              />
              <img
                  v-else
                  :src="baseUrl + scope.row.tupian.split(',')[0]"
                  width="80"
                  height="80"
                  style="object-fit: cover; border-radius: 4px;"
              />
            </div>
            <div v-else></div>
          </template>
        </el-table-column>
        <el-table-column
            :resizable="true"
            :sortable="false"
            prop="zuowei"
            label=""
            min-width="100"
            align="center"
        >
          <template slot-scope="scope">
            {{ scope.row.zuowei }}
          </template>
        </el-table-column>
        <el-table-column
            :resizable="true"
            :sortable="false"
            prop="qiandaozhuangtai"
            label=""
            min-width="100"
            align="center"
        >
          <template slot-scope="scope">
            <el-tag :type="scope.row.qiandaozhuangtai === '' ? 'success' : 'warning'">
              {{ scope.row.qiandaozhuangtai }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column
            :resizable="true"
            :sortable="false"
            prop="qiantuizhuangtai"
            label=""
            min-width="100"
            align="center"
        >
          <template slot-scope="scope">
            <el-tag :type="scope.row.qiantuizhuangtai === '' ? 'info' : 'warning'">
              {{ scope.row.qiantuizhuangtai }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column
            :resizable="true"
            :sortable="false"
            prop="yuyueshijian"
            label=""
            min-width="160"
            align="center"
        >
          <template slot-scope="scope">
            {{ scope.row.yuyueshijian }}
          </template>
        </el-table-column>
        <el-table-column
            :resizable="true"
            :sortable="false"
            prop="beizhu"
            label=""
            min-width="120"
            align="center"
        >
          <template #default="{ row }">
            <div class="remark-box">
              {{ row.qiantuibeizhu || "" }}
            </div>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column
            label=""
            min-width="280"
            align="center"
        >
          <template slot-scope="scope">
            <div style="display: flex; gap: 6px; justify-content: center; align-items: center; flex-wrap: wrap;">
              <!--  -->
              <el-button
                  type="primary"
                  size="mini"
                  icon="el-icon-view"
                  @click="toDetail(scope.row)"
                  style="border-radius: 4px; padding: 0 10px; height: 32px; line-height: 32px; transition: all 0.2s; border-color: #409EFF; background: #409EFF; color: #fff;"
              ></el-button
              >
              <!--  -->
              <el-button
                  type="danger"
                  size="mini"
                  icon="el-icon-circle-close"
                  @click="cancelOrder(scope.row)"
                  style="border-radius: 4px; padding: 0 10px; height: 32px; line-height: 32px; transition: all 0.2s; border-color: #F56C6C; background: #F56C6C; color: #fff;"
              ></el-button
              >
              <!--  -->
              <el-button
                  size="mini"
                  icon="el-icon-check"
                  v-if="scope.row.qiandaozhuangtai === ''"
                  @click="signIn(scope.row)"
                  style="border-radius: 4px; padding: 0 10px; height: 32px; line-height: 32px; transition: all 0.2s; border-color: #67C23A; background: #67C23A; color: #fff;"
              ></el-button
              >
              <!--  -->
              <el-button
                  size="mini"
                  icon="el-icon-arrow-right"
                  v-if="scope.row.qiantuizhuangtai === ''"
                  @click="signOut(scope.row)"
                  style="border-radius: 4px; padding: 0 10px; height: 32px; line-height: 32px; transition: all 0.2s; border-color: #909399; background: #909399; color: #fff;"
              ></el-button
              >
            </div>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
          @size-change="sizeChangeHandle"
          @current-change="currentChangeHandle"
          :current-page="pageIndex"
          background
          :page-sizes="[10, 20, 30, 50]"
          :page-size="pageSize"
          :layout="layouts.join()"
          :total="total"
          prev-text="<"
          next-text=">"
          :hide-on-single-page="true"
          :style="{
          width: '100%',
          padding: '20px 0',
          margin: '0 auto',
          whiteSpace: 'nowrap',
          color: '#333',
          fontWeight: '500',
          textAlign: 'center',
          boxSizing: 'border-box',
        }"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      baseUrl: '',
      breadcrumbItem: [{ name: '' }],
      searchForm: {
        yuyuedanhao: '',
        qiandaozhuangtai: '',
        qiantuizhuangtai: ''
      },
      dataList: [],
      pageIndex: 1,
      pageSize: 10,
      total: 0, // 
      totalPage: 0,
      dataListLoading: false,
      layouts: ["total", "prev", "pager", "next", "sizes", "jumper"],
      qiandaozhuangtaiOptions: ",".split(","),
      qiantuizhuangtaiOptions: ",".split(",")
    };
  },
  created() {
    this.baseUrl = this.$config.baseUrl;
    this.init();
    this.getDataList();
  },
  methods: {
    init() {
      this.qiandaozhuangtaiOptions = ",".split(",");
      this.qiantuizhuangtaiOptions = ",".split(",");
    },
    search() {
      this.pageIndex = 1;
      this.getDataList();
    },
    qiandaozhuangtaiChange() {},
    qiantuizhuangtaiChange() {},
    getDataList() {
      this.dataListLoading = true;
      const studentId = localStorage.getItem('userid');
      if (!studentId) {
        this.$message.error('');
        this.$router.push('/login');
        this.dataListLoading = false;
        return;
      }

      let params = {
        page: this.pageIndex,
        limit: this.pageSize,
        sfsh: ''
      };

      // 
      if (this.searchForm.yuyuedanhao) {
        params["yuyuedanhao"] = "%" + this.searchForm.yuyuedanhao + "%";
      }
      if (this.searchForm.qiandaozhuangtai) {
        params["qiandaozhuangtai"] = this.searchForm.qiandaozhuangtai;
      }
      if (this.searchForm.qiantuizhuangtai) {
        params["qiantuizhuangtai"] = this.searchForm.qiantuizhuangtai;
      }

      // get
      this.$http.get('yuyuexinxi/list', {params: params}).then(res => {
        if (res.data.code == 0) {
          this.dataList = res.data.data.list;
          this.total = res.data.data.total; // 
          this.pageSize = res.data.data.pageSize;
          this.totalPage = res.data.data.totalPage;
          console.log(':', this.dataList);
        } else {
          this.dataList = [];
          this.total = 0;
          this.totalPage = 0;
        }
        this.dataListLoading = false;
      }).catch(() => {
        this.dataListLoading = false;
        this.$message.error('');
      });
    },
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    toDetail(row) {
      this.$router.push({
        path: '/index/yuyuexinxiDetail',
        query: { detailObj: JSON.stringify(row) }
      });
    },
    // 
    cancelOrder(row) {
      if (!row.zixishiid) {
        console.warn('ID(zixishiid)');
      }
      this.$router.push({
        path: '/index/yuyuexinxi/quxiaoyuyue',
        query: {
          orderData: JSON.stringify(row)
        }
      });
    },
    // 
    signIn(row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'success'
      }).then(() => {
        // 1
        // 2getparams
        this.$http.post('yuyuexinxi/update', {
          id: row.id,
          qiandaozhuangtai: ''
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message.success('');
            this.getDataList(); // 
          } else {
            this.$message.error(data.msg || '');
          }
        }).catch(() => {
          this.$message.error('');
        });
      }).catch(() => {
        this.$message.info('');
      });
    },
    // 
    signOut(row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'info'
      }).then(() => {
        // 1
        // 2getparams
        this.$http.post('yuyuexinxi/update', {
          id: row.id,
          qiantuizhuangtai: ''
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message.success('');
            this.getDataList(); // 
          } else {
            this.$message.error(data.msg || '');
          }
        }).catch(() => {
          this.$message.error('');
        });
      }).catch(() => {
        this.$message.info('');
      });
    }
  }
};
</script>

<style lang="scss" scoped>
* {
  box-sizing: border-box;
}

.breadcrumb-preview .el-breadcrumb >>> .el-breadcrumb__separator {
  margin: 0 9px;
  color: #000;
  font-weight: 500;
}
.breadcrumb-preview .el-breadcrumb >>> .el-breadcrumb__inner {
  color: #000;
  display: inline-block;
}

.custom-table {
  border-radius: 8px;
  overflow: visible !important;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;

  .el-table__header-wrapper {
    th {
      background: #f8f9fa;
      color: #495057;
      font-weight: 600;
      font-size: 14px;
      border-bottom: 2px solid #e9ecef !important;
      padding: 12px 0;
    }
  }

  .el-table__body-wrapper {
    overflow: visible !important;
    .el-table__row {
      transition: all 0.3s ease;
      &:hover {
        background: #f1f3f5;
      }
      td {
        border-color: #e9ecef !important;
        padding: 12px 5px;
      }
    }
  }

  .index-column .index-badge {
    display: inline-block;
    width: 24px;
    height: 24px;
    line-height: 24px;
    border-radius: 50%;
    background: #93c7b3;
    color: white;
    font-weight: 500;
  }

  .text-ellipsis {
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .remark-box {
    padding: 4px 8px;
    background: #f8f9fa;
    border-radius: 4px;
    font-size: 13px;
    max-width: 200px;
    margin: 0 auto;
  }
}

.center-form-pv {
  .el-input, .el-select {
    width: 100%;
  }
  .el-input__inner {
    border: 1px solid #78abc3;
    border-radius: 4px;
    padding: 0 12px;
    box-shadow: 0px 2px 2px 0px #78abc3;
    outline: none;
    color: #000;
    font-size: 14px;
    height: 40px;
  }
  .el-select .el-input__inner {
    border: 1px solid #78abc3;
    border-radius: 4px;
    padding: 0 10px;
    box-shadow: 0px 2px 2px 0px #78abc3;
    outline: none;
    color: #000;
    font-size: 14px;
    height: 40px;
  }
}

.el-pagination >>> {
  .el-pagination__total {
    margin: 0 10px 0 0;
    color: #666;
    font-weight: 400;
  }
  .el-pager .number {
    cursor: pointer;
    padding: 0 4px;
    margin: 0 5px;
    color: #666;
    background: #f4f4f5;
    min-width: 30px;
    height: 28px;
    line-height: 28px;
    border-radius: 2px;
  }
  .el-pager .number:hover, .el-pager .number.active {
    background: #2e61e1;
    color: #fff;
  }
}

/*  */
.el-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}

/*  */
.justifyContent {
  justify-content: center;
}
.objectFit {
  object-fit: cover;
}

/*  */
@media (max-width: 768px) {
  .el-table-column--label {
    min-width: 80px !important;
  }
  .el-table-column__content {
    font-size: 12px;
  }
  .el-button {
    font-size: 12px;
    padding: 0 8px !important;
  }
}
</style>