<template>
  <div class="addEdit-block" style="width: 100%">
    <!--  -->
    <div :style='{"width":"100%","padding":"20px","margin":"10px auto","borderRadius":"27px","background":"#bfd2fc","boxSizing":"border-box"}' class="breadcrumb-preview">
      <el-breadcrumb :separator="''" :style='{"fontSize":"14px","lineHeight":"1"}'>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <el-form
        :style="{
        padding: '30px',
        boxShadow: '0 0px 0px rgba(64, 158, 255, .3)',
        borderRadius: '6px',
        flexWrap: 'wrap',
        background: '#fff',
        display: 'flex',
        width: '100%',
        maxWidth: '1200px',
        margin: '20px auto'
      }"
        class="add-update-preview"
        ref="ruleForm"
        :model="ruleForm"
        :rules="rules"
        label-width="100px"
    >
      <template>
        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="yuyuedanhao"
        >
          <el-input
              v-model="ruleForm.yuyuedanhao"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="mingcheng"
        >
          <el-input
              v-model="ruleForm.mingcheng"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="upload"
            label=""
            prop="tupian"
        >
          <img
              v-if="ruleForm.tupian && ruleForm.tupian.substring(0, 4) == 'http'"
              :src="ruleForm.tupian.split(',')[0]"
              width="100"
              height="100"
              style="objectFit: cover; borderRadius: 4px;"
          />
          <img
              v-else-if="ruleForm.tupian"
              :src="baseUrl + ruleForm.tupian.split(',')[0]"
              width="100"
              height="100"
              style="objectFit: cover; borderRadius: 4px;"
          />
          <span v-else></span>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="zuowei"
        >
          <el-input
              v-model="ruleForm.zuowei"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="yuyueshijian"
        >
          <el-input
              v-model="ruleForm.yuyueshijian"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="date"
            label=""
            prop="quxiaoshijian"
        >
          <el-date-picker
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="ruleForm.quxiaoshijian"
              type="datetime"
              placeholder=""
              readonly
          ></el-date-picker>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="xuehao"
        >
          <el-input
              v-model="ruleForm.xuehao"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="xingming"
        >
          <el-input
              v-model="ruleForm.xingming"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '50%', margin: '0 0 20px 0' }"
            class="input"
            label=""
            prop="shouji"
        >
          <el-input
              v-model="ruleForm.shouji"
              placeholder=""
              readonly
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item
            :style="{ width: '100%', margin: '0 0 20px 0' }"
            class="textarea"
            label=""
            prop="quxiaoyuanyin"
        >
          <el-input
              style="width: 100%; max-width: 800px"
              type="textarea"
              :rows="5"
              placeholder=""
              v-model="ruleForm.quxiaoyuanyin"
          >
          </el-input>
        </el-form-item>
      </template>

      <!--  -->
      <el-form-item
          :style="{ width: '100%', padding: '0', margin: '0', textAlign: 'center' }"
          class="btn"
      >
        <el-button
            :style="{
            border: '0',
            cursor: 'pointer',
            padding: '0',
            margin: '0 20px 0 0',
            outline: 'none',
            color: 'rgba(255, 255, 255, 1)',
            borderRadius: '30px',
            background: '#93C7B3',
            width: '128px',
            lineHeight: '40px',
            fontSize: '14px',
            height: '40px',
          }"
            type="primary"
            class="btn-success"
            @click="onSubmit"
        ></el-button
        >
        <el-button
            :style="{
            border: '0px solid rgba(64, 158, 255, 1)',
            cursor: 'pointer',
            padding: '0',
            margin: '0',
            outline: 'none',
            color: '#fff',
            borderRadius: '30px',
            background: '#93C7B3',
            width: '128px',
            lineHeight: '40px',
            fontSize: '14px',
            height: '40px',
          }"
            class="btn-close"
            @click="back()"
        ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// url
import { isMobile } from "@/utils/validate";
export default {
  data() {
    let self = this;
    var validateMobile = (rule, value, callback) => {
      if (!value) {
        callback();
      } else if (!isMobile(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      baseUrl: '',
      ruleForm: {
        yuyuedanhao: "",
        mingcheng: "",
        tupian: "",
        zuowei: "",
        yuyueshijian: "",
        quxiaoshijian: "",
        quxiaoyuanyin: "",
        xuehao: "",
        xingming: "",
        shouji: "",
        yuyueId: "", // ID
        zixishiid: "" // ID
      },
      rules: {
        quxiaoyuanyin: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 200, message: '5-200', trigger: 'blur' }
        ],
        shouji: [{ validator: validateMobile, trigger: "blur" }]
      }
    };
  },
  created() {
    this.baseUrl = this.$config.baseUrl;
    // 
    this.ruleForm.quxiaoshijian = this.getCurDateTime();
    // list.vue
    this.initOrderData();
  },
  methods: {
    // yyyy-MM-dd HH:mm:ss
    getCurDateTime() {
      const date = new Date();
      const y = date.getFullYear();
      const m = (date.getMonth() + 1).toString().padStart(2, '0');
      const d = date.getDate().toString().padStart(2, '0');
      const h = date.getHours().toString().padStart(2, '0');
      const min = date.getMinutes().toString().padStart(2, '0');
      const s = date.getSeconds().toString().padStart(2, '0');
      return `${y}-${m}-${d} ${h}:${min}:${s}`;
    },

    // 
    initOrderData() {
      console.log(':', this.$route.query);

      let order = null;

      // orderData
      if (this.$route.query.orderData) {
        try {
          order = JSON.parse(this.$route.query.orderData);
          console.log(':', order);
        } catch (e) {
          console.error('orderData:', e);
        }
      }

      // 
      if (!order) {
        this.$message.error('');
        this.back();
        return;
      }

      // 
      this.ruleForm.yuyuedanhao = order.yuyuedanhao || '';
      this.ruleForm.mingcheng = order.mingcheng || '';
      this.ruleForm.tupian = order.tupian || '';
      this.ruleForm.zuowei = order.zuowei || '';
      this.ruleForm.yuyueshijian = order.yuyueshijian || '';
      this.ruleForm.xuehao = order.xuehao || '';
      this.ruleForm.xingming = order.xingming || '';
      this.ruleForm.shouji = order.shouji || '';
      this.ruleForm.yuyueId = order.id || '';
      // ID
      this.ruleForm.zixishiid = order.zixishiid || '';

      console.log(':', this.ruleForm);
    },

    // 
    onSubmit() {
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          // crossrefidID
          const submitData = {
            crossrefid: this.ruleForm.yuyueId, // IDcrossrefid
            yuyueId: this.ruleForm.yuyueId,
            yuyuedanhao: this.ruleForm.yuyuedanhao,
            mingcheng: this.ruleForm.mingcheng,
            zuowei: this.ruleForm.zuowei,
            quxiaoshijian: this.ruleForm.quxiaoshijian,
            quxiaoyuanyin: this.ruleForm.quxiaoyuanyin,
            xuehao: this.ruleForm.xuehao,
            xingming: this.ruleForm.xingming,
            shouji: this.ruleForm.shouji,
            zixishiid: this.ruleForm.zixishiid // ID
          };

          console.log(':', submitData);

          this.$http.post('quxiaoyuyue/save', submitData).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "",
                type: "success",
                duration: 1500,
                onClose: () => {
                  // 
                  this.$router.push('/index/yuyuexinxi');
                }
              });
            } else {
              this.$message.error(data.msg || '');
            }
          }).catch((error) => {
            console.error(':', error);
            this.$message.error('');
          });
        } else {
          this.$message.error('');
        }
      });
    },

    // 
    back() {
      this.$router.push('/index/yuyuexinxi');
    }
  }
};
</script>
