                background: '#fff'
              }"></div>
              <span></span>
            </div>
            <div :style="{display: 'flex', alignItems: 'center', gap: '6px'}">
              <div :style="{
                width: '16px',
                height: '16px',
                border: '2px solid #2E61E1',
                borderRadius: '4px',
                background: '#2E61E1',
                boxShadow: '0 0 0 2px rgba(46, 97, 225, 0.3)'
              }"></div>
              <span></span>
            </div>
            <div :style="{display: 'flex', alignItems: 'center', gap: '6px'}">
              <div :style="{
                width: '16px',
                height: '16px',
