          maxWidth: '500px',
          margin: '0 auto'
        }">
          <i class="el-icon-info" :style="{color: '#2E61E1', marginRight: '6px'}"></i>
          <span style="color: #2e61e1; font-weight: 500;">{{ selectedSeatsText }}</span>
          <span style="marginLeft: 10px; color: '#999';">{{ maxSelect }}</span>
        </div>

        <div v-else style="padding: 20px; color: #666; text-align: center;">...</div>
