<template>
  <div :style='{"width":"80%","margin":"10px auto","position":"relative","background":"#fff","boxShadow":"0px 4px 10px 0px rgba(0,0,0,0.302)"}'>
    <!--  -->
    <div class="title" :style='{"margin":"0","color":"#333","textAlign":"center","background":"rgba(0,0,0,0.102)","fontSize":"20px","lineHeight":"54px","fontWeight":"600"}'>{{ title }}</div>

    <!--  +  -->
    <div :style='{"display":"flex","padding":"20px"}'>
      <!-- ++ -->
      <div :style='{"width":"180px","textAlign":"center","background":"#f5f7fa","borderRadius":"8px","padding":"20px","marginRight":"20px"}'>
        <!--  -->
        <div :style='{"marginBottom":"15px","cursor":"pointer"}' @click="triggerAvatarUpload">
          <img
              v-if="sessionForm.touxiang"
              :src="baseUrl + sessionForm.touxiang"
              :style='{"width":"100px","height":"100px","borderRadius":"50%","border":"2px solid #fff","boxShadow":"0 2px 8px rgba(0,0,0,0.1)"}'
          >
          <!--  -->
          <div
              v-else
              :style='{"width":"100px","height":"100px","borderRadius":"50%","background":"#e6f0ff","color":"#409eff","fontSize":"36px","lineHeight":"100px","display":"inline-block","textAlign":"center"}'
          >
            <i class="el-icon-user"></i>
          </div>
          <!--  -->
          <el-upload
              ref="avatarUploader"
              class="avatar-uploader"
              :action="uploadUrl"
              :headers="headers"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              style="display: none;"
          >
            <input type="file" accept="image/*">
          </el-upload>
        </div>
        <!--  -->
        <div :style='{"fontSize":"14px","color":"#333","padding":"8px 0","background":"#fff","borderRadius":"4px","marginBottom":"20px"}'>
          {{ sessionForm.xuehao || '' }}
        </div>
        <!--  -->
        <div :style='{"display":"flex","flexDirection":"column","gap":"10px"}'>
          <el-button
              type="primary"
              plain
              @click="switchTab('')"
              :style='{
              width:"100%",background:activeTab===""?"#409EFF":"#fff",
              color:activeTab===""?"#fff":"#409EFF",
              borderColor:activeTab===""?"#409EFF":"#dcdfe6",
              borderRadius:"4px"
            }'
          >
            
          </el-button>
          <el-button
              type="primary"
              plain
              @click="triggerAvatarUpload"
              :style='{
              width:"100%",background:activeTab===""?"#409EFF":"#fff",
              color:activeTab===""?"#fff":"#409EFF",
              borderColor:activeTab===""?"#409EFF":"#dcdfe6",
              borderRadius:"4px"
            }'
          >
            
          </el-button>
          <el-button
              type="primary"
              plain
              @click="switchTab('')"
              :style='{
              width:"100%",background:activeTab===""?"#409EFF":"#fff",
              color:activeTab===""?"#fff":"#409EFF",
              borderColor:activeTab===""?"#409EFF":"#dcdfe6",
              borderRadius:"4px"
            }'
          >
            
          </el-button>
          <!--  -->
          <el-button
              type="primary"
              plain
              @click="switchTab('')"
              :style='{
              width:"100%",background:activeTab===""?"#409EFF":"#fff",
              color:activeTab===""?"#fff":"#409EFF",
              borderColor:activeTab===""?"#409EFF":"#dcdfe6",
              borderRadius:"4px"
            }'
          >
            
          </el-button>
        </div>
      </div>

      <!--  -->
      <div :style='{"flex":"1","background":"rgba(0,0,0,0.102)","borderRadius":"8px","padding":"30px", "display":"flex", "justify-content":"center", "align-items":"flex-start"}'>
        <!--  -->
        <div v-if="activeTab === ''">
          <el-form ref="sessionForm" :model="sessionForm" :rules="rules" label-width="80px" :style='{"width":"100%","maxWidth":"500px"}'>
            <el-form-item label="" prop="xingming" :style='{"marginBottom":"20px"}'>
              <el-input v-model="sessionForm.xingming" placeholder="" style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item label="" :style='{"marginBottom":"20px"}'>
              <el-select v-model="sessionForm.xingbie" placeholder="" style="width: 300px;">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="shouji" :style='{"marginBottom":"20px"}'>
              <el-input v-model="sessionForm.shouji" placeholder="" style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item label="" prop="banji" :style='{"marginBottom":"20px"}'>
              <el-input v-model="sessionForm.banji" placeholder="" style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                  type="primary"
                  @click="onSubmit('sessionForm')"
                  :style='{"background":"#409EFF","color":"#fff","border":"none","padding":"0 20px","height":"40px"}'
              >
                
              </el-button>
            </el-form-item>
          </el-form>
        </div>

        <!--  -->
        <div v-if="activeTab === ''" :style='{"textAlign":"center","padding":"50px 0"}'>
          <p :style='{"fontSize":"16px","color":"#666","marginBottom":"20px"}'></p>
        </div>

        <!--  -->
        <div v-if="activeTab === ''">
          <el-form ref="pwdForm" :model="pwdForm" :rules="pwdRules" label-width="80px" :style='{"width":"100%","maxWidth":"500px"}'>
            <el-form-item label="" prop="oldPassword" :style='{"marginBottom":"20px"}'>
              <el-input v-model="pwdForm.oldPassword" type="password" placeholder="" style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item label="" prop="newPassword" :style='{"marginBottom":"20px"}'>
              <el-input v-model="pwdForm.newPassword" type="password" placeholder="" style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item label="" prop="confirmPassword" :style='{"marginBottom":"20px"}'>
              <el-input v-model="pwdForm.confirmPassword" type="password" placeholder="" style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                  type="primary"
                  @click="updatePassword"
                  :style='{"background":"#409EFF","color":"#fff","border":"none","padding":"0 20px","height":"40px"}'
              >
                
              </el-button>
            </el-form-item>
          </el-form>
        </div>

        <!--  -->
        <div v-if="activeTab === ''" :style='{"width":"100%","padding":"20px"}'>
          <!--  -->
          <el-form
              class="center-form-pv"
              :style="{ width: '100%', maxWidth: '800px', margin: '0 auto 20px' }"
              :inline="true"
              :model="searchForm"
          >
            <el-row :style="{ display: 'flex', alignItems: 'center', gap: '10px' }">
              <div :style="{ display: 'flex', flexDirection: 'column', flex: 1 }">
                <label
                    :style="{
                    color: '#000',
                    fontSize: '14px',
                    fontWeight: '500',
                    marginBottom: '4px'
                  }"
                ></label>
                <el-input
                    v-model="searchForm.yuyuedanhao"
                    placeholder=""
                    clearable
                    :style="{ width: '100%' }"
                ></el-input>
              </div>
              <el-button
                  :style="{
                  border: '1px solid #97C9D6',
                  cursor: 'pointer',
                  padding: '0 24px',
                  boxShadow: '0px 2px 2px 0px #78ABC3',
                  outline: 'none',
                  color: '#78ABC3',
                  borderRadius: '30px',
                  background: '#fff',
                  fontSize: '14px',
                  height: '40px',
                  alignSelf: 'flex-end'
                }"
                  type="success"
                  @click="searchCancelOrder"
              ></el-button>
            </el-row>
          </el-form>

          <!--  -->
          <el-table
              class="custom-table"
              :style="{
              width: '100%',
              background: '#fff',
              borderRadius: '8px',
              boxShadow: '0 2px 12px rgba(0,0,0,0.08)'
            }"
              :data="cancelOrderList"
              v-loading="cancelOrderLoading"
          >
            <el-table-column type="index" label="" width="80" align="center"></el-table-column>
            <el-table-column prop="yuyuedanhao" label="" min-width="150" align="center"></el-table-column>
            <el-table-column prop="mingcheng" label="" min-width="120" align="center"></el-table-column>
            <el-table-column prop="yuyueshijian" label="" min-width="180" align="center"></el-table-column>
            <el-table-column prop="quxiaoshijian" label="" min-width="180" align="center"></el-table-column>
            <el-table-column prop="xuehao" label="" min-width="120" align="center"></el-table-column>
            <el-table-column prop="xingming" label="" min-width="120" align="center"></el-table-column>
            <el-table-column prop="shouji" label="" min-width="150" align="center"></el-table-column>
          </el-table>

          <!-- layout -->
          <el-pagination
              @size-change="sizeChangeHandle"
              @current-change="currentChangeHandle"
              :current-page="pageIndex"
              background
              :page-sizes="[10, 20, 30, 50]"
              :page-size="pageSize"
              :layout="'total, prev, pager, next, sizes, jumper'"
              :total="totalPage"
              :style="{ width: '100%', margin: '20px 0 0', textAlign: 'center' }"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from '@/config/config'
export default {
  data() {
    // $validate.isMobile
    const validateMobile = (rule, value, callback) => {
      if (!value) {
        callback();
      } else {
        const mobileReg = /^1[3-9]\d{9}$/;
        if (!mobileReg.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      }
    };

    return {
      title: '',
      activeTab: '',
      baseUrl: config.baseUrl,
      sessionForm: {},
      pwdForm: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      rules: {
        xingming: [{ required: true, message: '', trigger: 'blur' }],
        shouji: [{ validator: validateMobile, trigger: 'blur' }]
      },
      pwdRules: {
        oldPassword: [
          { required: true, message: '', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          {
            validator: (rule, value, callback) => {
              if (value !== this.pwdForm.newPassword) {
                callback(new Error(''));
              } else {
                callback();
              }
            },
            trigger: 'blur'
          }
        ]
      },
      uploadUrl: config.baseUrl + 'file/upload',
      headers: { Token: localStorage.getItem('Token') },
      userTableName: localStorage.getItem('UserTableName'),
      // 
      searchForm: { yuyuedanhao: '' },
      cancelOrderList: [],
      cancelOrderLoading: false,
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0
    }
  },
  created() {
    this.getSession();
  },
  methods: {
    getSession() {
      this.$http.get(this.userTableName + '/session', { emulateJSON: true }).then(res => {
        if (res.data && res.data.code === 0) {
          this.sessionForm = res.data.data;
        }
      }).catch(() => {
        this.$message.error('');
      });
    },
    switchTab(tabName) {
      this.activeTab = tabName;
      this.title = tabName === '' ? '' : tabName;
      // 
      if (tabName === '') {
        this.pageIndex = 1;
        this.getCancelOrderList();
      }
    },
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$http.post(this.userTableName + '/update', this.sessionForm).then(res => {
            if (res.data && res.data.code === 0) {
              this.$message.success('');
            } else {
              this.$message.error(res.data?.msg || '');
            }
          }).catch(() => {
            this.$message.error('');
          });
        }
      });
    },
    updatePassword() {
      this.$refs['pwdForm'].validate(valid => {
        if (valid) {
          if (!this.sessionForm.mima) {
            this.$message.error('');
            return;
          }
          if (this.pwdForm.oldPassword !== this.sessionForm.mima) {
            this.$message.error('');
            return;
          }
          this.$http.post(this.userTableName + '/update', {
            id: this.sessionForm.id,
            mima: this.pwdForm.newPassword
          }).then(res => {
            if (res.data && res.data.code === 0) {
              this.$message.success('');
              this.pwdForm = { oldPassword: '', newPassword: '', confirmPassword: '' };
            } else {
              this.$message.error(res.data?.msg || '');
            }
          }).catch(() => {
            this.$message.error('');
          });
        }
      });
    },
    triggerAvatarUpload() {
      this.switchTab('');
      // uploader
      if (this.$refs.avatarUploader) {
        const fileInput = this.$refs.avatarUploader.$el.querySelector('input[type="file"]');
        if (fileInput) {
          fileInput.click();
        } else {
          this.$message.error('');
        }
      } else {
        this.$message.error('');
      }
    },
    handleAvatarSuccess(res) {
      if (res && res.code === 0) {
        this.sessionForm.touxiang = 'upload/' + res.file;
        this.$message.success('');
        this.switchTab('');
      } else {
        this.$message.error(res?.msg || '');
      }
    },
    // 
    searchCancelOrder() {
      this.pageIndex = 1;
      this.getCancelOrderList();
    },
    getCancelOrderList() {
      // 
      if (!this.sessionForm.xuehao) {
        this.$message.warning('');
        this.cancelOrderLoading = false;
        return;
      }

      this.cancelOrderLoading = true;
      const params = {
        page: this.pageIndex,
        limit: this.pageSize,
        sort: 'id',
        order: 'desc',
        // 
        xuehao: this.sessionForm.xuehao
      };
      if (this.searchForm.yuyuedanhao) {
        params.yuyuedanhao = `%${this.searchForm.yuyuedanhao}%`;
      }
      this.$http({
        url: 'quxiaoyuyue/page',
        method: 'get',
        params
      }).then(({ data }) => {
        this.cancelOrderLoading = false;
        if (data && data.code === 0) {
          this.cancelOrderList = data.data.list || [];
          this.totalPage = data.data.total || 0;
        } else {
          this.cancelOrderList = [];
          this.totalPage = 0;
          this.$message.warning(data?.msg || '');
        }
      }).catch(() => {
        this.cancelOrderLoading = false;
        this.$message.error('');
      });
    },
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.getCancelOrderList();
    },
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getCancelOrderList();
    }
  }
}
</script>

<style lang="scss" scoped>
/*  */
.el-form-item >>> .el-form-item__label {
  color: #333;
  font-weight: 500;
  text-align: right;
  line-height: 40px;
}
.el-input >>> .el-input__inner {
  border: 1px solid #ccc;
  border-radius: 4px;
  height: 40px;
  padding: 0 12px;
}
.el-select >>> .el-input__inner {
  border: 1px solid #ccc;
  border-radius: 4px;
  height: 40px;
  padding: 0 12px;
}
.el-icon-user {
  font-size: 36px !important;
  line-height: 100px !important;
}
.avatar-wrap:hover {
  opacity: 0.9;
  transition: opacity 0.2s;
}

/*  */
.center-form-pv .el-input >>> .el-input__inner {
  border: 1px solid #78abc3;
  border-radius: 4px;
  padding: 0 12px;
  box-shadow: 0px 2px 2px rgba(120, 171, 195, 0.3);
  outline: none;
  height: 40px;
}
.custom-table {
  .el-table__header-wrapper th {
    background: #f8f9fa;
    color: #495057;
    font-weight: 600;
  }
  .el-table__body-wrapper tr:hover td {
    background: rgba(147, 199, 179, 0.1);
  }
}
</style>